@import '../../styles/mixins';
@import '../../styles/constants';

.sb-toggle_on-label input {
  &:checked + .sb-toggle__switch__slider::after {
    content: var(--sb-toggle__on-label, 'On');
    padding-left: 12px;
    padding-right: 0;
  }
}

.sb-toggle_off-label input {
  + .sb-toggle__switch__slider::after {
    content: var(--sb-toggle__off-label, 'Off');
    padding-right: 8px;
  }
}

.sb-toggle_yes-label input {
  &:checked + .sb-toggle__switch__slider::after {
    content: var(--sb-toggle__on-label, 'Yes');
    padding-left: 8px;
    padding-right: 0;
  }
}

.sb-toggle_no-label input {
  + .sb-toggle__switch__slider::after {
    content: var(--sb-toggle__off-label, 'No');
    padding-right: 10px;
  }
}

.sb-toggle_check-label input {
  &:checked + .sb-toggle__switch__slider::after {
    background: transparent
      url($asset-url + '/fonts/FontAwesome/6.1.0/svgs/check.svg') no-repeat 12px
      center;
    background-size: 1rem;
    content: '';
    display: inline-block;
    filter: invert(100%);
    height: 100%;
    padding-right: 0;
    width: 100%;
  }
}

.sb-toggle__switch {
  display: inline-block;
  height: 28px;
  position: relative;
  width: 60px;

  input {
    height: 0;
    opacity: 0;
    width: 0;

    &:checked + .sb-toggle__switch__slider::before {
      -webkit-transform: translateX(32px);
      -ms-transform: translateX(32px);
      transform: translateX(32px);
    }
  }
}

.sb-toggle__switch__slider {
  border-radius: 34px;
  bottom: 0;
  box-sizing: border-box;
  color: var(--sbt-white);
  cursor: pointer;
  font-size: var(--sbt-font-size-20);
  left: 0;
  line-height: 29px;
  position: absolute;
  right: 0;
  text-align: right;
  text-transform: uppercase;
  top: 0;
  transition: all 0.2s ease-out;

  &::before {
    background-color: var(--sbt-white);
    border-radius: var(--sbt-border-radius-circle);
    bottom: 4px;
    content: '';
    height: 20px;
    left: 4px;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 20px;
  }
}

.sb-toggle__switch__slider {
  background-color: var(
    --sb-toggle-unchecked-color,
    var(--sbt-theme-tertiary-color-30)
  );
}

.sb-toggle__switch input:checked + .sb-toggle__switch__slider {
  background-color: var(--sb-toggle-checked-color, var(--sbt-green-30));
  text-align: left;
}

.sb-toggle__switch input:focus + .sb-toggle__switch__slider {
  box-shadow: var(--sbt-elevation-40);
}

.sb-toggle_disabled {
  .sb-toggle__switch__slider {
    background-color: var(--sbt-theme-tertiary-color-30);
    cursor: not-allowed;

    &::before {
      background-color: var(--sbt-theme-tertiary-color-20);
    }
  }

  input:checked + .sb-toggle__switch__slider {
    background-color: var(--sbt-theme-tertiary-color-30);
  }

  .sb-toggle__switch__slider-shake {
    @include disabled-shake();
  }
}
